:root{
	--bg:#0f1724;
	--card:#0b1220;
	--muted:#9aa4b2;
	--accent:#5eead4;
	--accent-2:#60a5fa;
	--surface:#0b1220;
	--glass: rgba(255,255,255,0.03);
	--radius:12px;
	--gap:14px;
	--max-width:1100px;
}

*{box-sizing:border-box}
body{margin:0;background:linear-gradient(180deg,#071023 0%, #071a2a 100%);color:#e6eef6;font-family:Inter,system-ui,Segoe UI,Roboto,Helvetica,Arial,sans-serif}
.app-container{max-width:var(--max-width);margin:28px auto;padding:20px}
.brand{display:flex;align-items:center;gap:12px;margin-bottom:14px}
.brand h1{font-size:20px;margin:0}
.sub{color:var(--muted);font-size:13px}

.onboard-grid{display:grid;grid-template-columns:360px 1fr;gap:var(--gap)}
.card{background:var(--glass);padding:14px;border-radius:var(--radius);border:1px solid rgba(255,255,255,0.04)}

.camera-box{display:flex;flex-direction:column;gap:12px;align-items:center}
.video-wrap{width:320px;height:240px;border-radius:10px;overflow:hidden;background:#000;border:1px solid rgba(255,255,255,0.04)}
.video-wrap video{width:100%;height:100%;object-fit:cover;display:block}
.camera-actions{display:flex;gap:8px}
.btn{background:linear-gradient(90deg,var(--accent-2),var(--accent));border:none;color:#04243a;padding:8px 12px;border-radius:8px;cursor:pointer;font-weight:600}
.btn.ghost{background:transparent;border:1px solid rgba(255,255,255,0.06);color:var(--accent);}
.status{font-size:13px;color:var(--muted)}

.profile-setup{display:flex;flex-direction:column;gap:8px}
.input{padding:10px;border-radius:8px;border:1px solid rgba(255,255,255,0.04);background:transparent;color:inherit}
.input.small{width:220px}

.chat-root{display:flex;gap:18px;font-family:Inter,Arial,Helvetica,sans-serif}
.sidebar{width:260px;padding:16px;border-radius:12px;background:linear-gradient(180deg, rgba(255,255,255,0.02), rgba(255,255,255,0.01));border:1px solid rgba(255,255,255,0.04)}
.sidebar .btn{width:100%;margin-top:6px}
.chat-area{flex:1;display:flex;flex-direction:column;height:560px;border-radius:12px;background:linear-gradient(180deg, rgba(255,255,255,0.02), rgba(255,255,255,0.01));border:1px solid rgba(255,255,255,0.04)}
.chat-header{padding:12px;border-bottom:1px solid rgba(255,255,255,0.03);background:transparent;display:flex;justify-content:space-between;align-items:center}
.messages{flex:1;padding:16px;overflow:auto;background:transparent}
.msg{margin-bottom:12px;max-width:70%}
.msg.me{margin-left:auto;text-align:right}
.msg-from{font-size:12px;color:var(--muted);margin-bottom:6px}
.msg-text{background:linear-gradient(180deg,#041827,#052a3a);padding:10px;border-radius:10px;display:inline-block;color:#dff6ee}
.msg.me .msg-text{background:linear-gradient(180deg,#0f3b2b,#125a3d);color:#04243a}
.chat-controls{display:flex;gap:8px;padding:12px;border-top:1px solid rgba(255,255,255,0.03)}
.chat-controls input{flex:1;padding:10px;border-radius:10px;border:1px solid rgba(255,255,255,0.04);background:transparent;color:inherit}

.muted{color:var(--muted)}

@media (max-width:900px){
	.onboard-grid{grid-template-columns:1fr}
	.chat-area{height:420px}
	.sidebar{width:200px}
}

